/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.page-index {
  transition: background-color 500ms;
}
.page-index ._page-content {
  margin-right: auto;
  margin-left: auto;
  max-width: 980px;
}
.page-index ._section {
  margin-top: 160px;
}

.download-button {
  text-decoration: none;
  text-decoration-color: none;
  color: initial;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  font-family: "Montserrat", sans-serif;
  transition: color 500ms;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: 32px;
  padding-left: 32px;
  border-radius: 16px;
  text-transform: uppercase;
  font-weight: 700;
  color: var(--q-color-white, );
  background: #1D1D1D;
  background: var(--q-color-nightfall, #1D1D1D);
  border: thin solid;
  border: var(--q-color-ivory, ) thin solid;
}
.download-button:focus {
  outline: 0;
  box-shadow: none;
}
.download-button:focus {
  outline: 0;
  box-shadow: none;
}
.download-button:visited, .download-button:active, .download-button:focus, .download-button:hover {
  cursor: pointer;
}
.download-button:active {
  transform: scale(0.95);
}

.dark-mode .download-button {
  border: thin solid;
  border: var(--q-color-white, ) thin solid;
}