/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
/**
 * @param {number} $size - Size in pixels (without units). Default: 2
 * @param {hex} $color - Any hex color
 * @param {number} $correction - Corrects rounding and reduces text-shadows. For better understanding see this CodePen. Default: 0
 */
/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.pep-hero {
  background-image: linear-gradient(to right, rgba(190, 195, 214, 0.5) 1px, transparent 1px), linear-gradient(to bottom, rgba(190, 195, 214, 0.5) 1px, transparent 1px);
  background-position: -1px -1px;
}

.dark-mode .pep-hero {
  background-image: linear-gradient(to right, rgba(100, 104, 121, 0.1) 1px, transparent 1px), linear-gradient(to bottom, rgba(100, 104, 121, 0.1) 1px, transparent 1px);
  background-position: -1px -1px;
}

.pep-hero {
  background-size: 15px 15px;
  height: 134px;
}
@media screen and (min-width: 576px) {
  .pep-hero {
    background-size: 20px 20px;
  }
}
@media screen and (min-width: 768px) {
  .pep-hero {
    background-size: 20px 20px;
  }
}
@media screen and (min-width: 992px) {
  .pep-hero {
    background-size: 25px 25px;
  }
}
@media screen and (min-width: 576px) {
  .pep-hero {
    height: 179px;
  }
}
@media screen and (min-width: 768px) {
  .pep-hero {
    height: 179px;
  }
}
@media screen and (min-width: 992px) {
  .pep-hero {
    height: 224px;
  }
}

.pep-hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.pep-hero ._title {
  font-family: "VAL", "Montserrat", sans-serif !important;
  letter-spacing: 2pt;
  text-transform: uppercase;
  text-align: center;
  line-height: 0.8em;
  -webkit-text-stroke: 2px black;
  text-stroke: 2px black;
  font-size: 50px;
}
.pep-hero ._title > span {
  display: inline-block;
  transform: translateY(13%);
}
@media screen and (min-width: 576px) {
  .pep-hero ._title {
    -webkit-text-stroke: 3px black;
  }
}
@media screen and (min-width: 576px) {
  .pep-hero ._title {
    text-stroke: 3px black;
  }
}
@media screen and (min-width: 992px) {
  .pep-hero ._title {
    -webkit-text-stroke: 4px black;
  }
}
@media screen and (min-width: 992px) {
  .pep-hero ._title {
    text-stroke: 4px black;
  }
}
@media screen and (min-width: 360px) {
  .pep-hero ._title {
    font-size: 60px;
  }
}
@media screen and (min-width: 576px) {
  .pep-hero ._title {
    font-size: 80px;
  }
}
@media screen and (min-width: 768px) {
  .pep-hero ._title {
    font-size: 100px;
  }
}
@media screen and (min-width: 992px) {
  .pep-hero ._title {
    font-size: 140px;
  }
}
.pep-hero ._title > span:first-child {
  color: #FD453E;
  color: var(--q-color-sig-red, #FD453E);
}
.pep-hero ._title > span:last-child {
  color: #5FA8EE;
  color: var(--q-color-sig-blue, #5FA8EE);
}