 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.icon-grid {
  display: grid;
  min-width: 300px;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 100px);
  grid-auto-rows: 100px;
  grid-gap: 24px;
}

.anim-grid-item {
  width: 100px;
  height: 100px;
  transition: all 250ms;
  opacity: 1;
}
.anim-grid-item > * {
  height: 100%;
  width: 100%;
}

.anim-grid-enter, .anim-grid-leave-to {
  opacity: 0;
}

.anim-grid-leave-active {
  display: none;
}