 * * +C(background, secondary)
 * */
/**
 * @param {number} $size - Size in pixels (without units). Default: 2
 * @param {hex} $color - Any hex color
 * @param {number} $correction - Corrects rounding and reduces text-shadows. For better understanding see this CodePen. Default: 0
 */
/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.icon-tile {
  background-color: var(--q-color-white, );
}

.dark-mode .icon-tile {
  background-color: #202020;
  background-color: var(--q-color-moonlight, #202020);
}

.icon-tile {
  transition: background-color 500ms;
  border-radius: 8px;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-tile ._svg {
  cursor: zoom-in;
  flex: 1;
}
.icon-tile ._name {
  height: 36px;
  font-size: 12px;
  line-height: 1.4em;
  width: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}