/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.pep-input .q-field__control {
  transition: background-color 500ms;
  border-radius: 16px;
  background-color: var(--q-color-white, );
}
.pep-input .q-field__control:before {
  border: none;
  transition: border-color 0.36s cubic-bezier(0.4, 0, 0.2, 1);
}
.pep-input .q-field__control:hover {
  background-color: var(--q-color-white, );
}
.pep-input.q-field--focused .q-field__control {
  background-color: var(--q-color-white, );
}

.dark-mode .pep-input .q-field__control {
  background-color: #202020;
  background-color: var(--q-color-moonlight, #202020);
}
.dark-mode .pep-input .q-field__control:hover {
  background-color: #202020;
  background-color: var(--q-color-moonlight, #202020);
}
.dark-mode .pep-input.q-field--focused .q-field__control {
  background-color: #202020;
  background-color: var(--q-color-moonlight, #202020);
}