/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
/**
 * @param {number} $size - Size in pixels (without units). Default: 2
 * @param {hex} $color - Any hex color
 * @param {number} $correction - Corrects rounding and reduces text-shadows. For better understanding see this CodePen. Default: 0
 */
/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.pep-link {
  text-decoration: none;
  text-decoration-color: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #333333;
  color: var(--q-color-letters, #333333);
}
.pep-link:focus {
  outline: 0;
  box-shadow: none;
}
.pep-link._has-icon {
  font-weight: 500;
}

.dark-mode .pep-link {
  color: var(--q-color-white, );
}

.pep-link ._icon {
  color: #1D1D1D;
  color: var(--q-color-nightfall, #1D1D1D);
}

.dark-mode .pep-link ._icon {
  color: var(--q-color-white, );
}

.pep-link._retro-underline {
  position: relative;
  display: inline-block;
}
.pep-link._retro-underline::after, .pep-link._retro-underline::before {
  content: "";
  position: absolute;
  border-radius: 50px;
  transform: rotate(181deg) translateY(-2px);
  transition: border-color 250ms;
  width: 105%;
}
.pep-link._retro-underline::after {
  bottom: 0;
  left: -2px;
  border-top: 2px solid;
  border-top: var(--q-color-black, ) 2px solid;
}
.pep-link._retro-underline::before {
  justify-content: bottom;
  transform-origin: bottom;
  bottom: 1px;
  left: -2px;
  border-bottom: #AB92F0 6px solid;
  border-bottom: var(--q-color-primary, #AB92F0) 6px solid;
}

.dark-mode.pop-mode .pep-link._retro-underline::after {
  border-top: rgba(0, 0, 0, 0) 2px solid;
  border-top: var(--q-color-none, rgba(0, 0, 0, 0)) 2px solid;
}

.dark-mode.print-mode .pep-link._retro-underline::after {
  border-top: #AB92F0 2px solid;
  border-top: var(--q-color-primary, #AB92F0) 2px solid;
}
.dark-mode.print-mode .pep-link._retro-underline::before {
  border-bottom: 6px solid;
  border-bottom: var(--q-color-black, ) 6px solid;
}