 */
/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.profile-card {
  background: var(--q-color-white, );
}

.dark-mode .profile-card {
  background: #202020;
  background: var(--q-color-moonlight, #202020);
}

.profile-card {
  width: 310px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  border-radius: 16px;
  position: relative;
}
.profile-card > * {
  position: relative;
  z-index: 1;
}
.profile-card ._svg-elements-outer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}
.profile-card ._svg-elements-inner {
  position: relative;
}
.profile-card ._svg-elements-inner svg {
  position: absolute;
}
.profile-card ._square {
  left: 15px;
  top: 100px;
}
.profile-card ._mountain-left {
  left: 35px;
  top: 40px;
}
.profile-card ._triangle {
  left: 55px;
  top: 5px;
}
.profile-card ._circle {
  right: 30px;
  top: 20px;
}
.profile-card ._mountain-right {
  right: 20px;
  top: 120px;
}
.profile-card ._profile-pic {
  display: flex;
  justify-content: center;
  height: 130px;
  width: 130px;
  border-radius: 50%;
  overflow: hidden;
}
.profile-card ._profile-pic > img {
  height: 130px;
  width: auto;
}
.profile-card ._description {
  text-align: left;
  line-height: 40px;
}
.profile-card ._link {
  cursor: pointer;
}
.profile-card ._link > * {
  color: #bec3d6;
  color: var(--q-color-washed-cloth, #bec3d6);
}